import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faRandom,
  faAngleLeft,
  faAngleRight,
  faSyncAlt,
  faPlay,
  faPause,
} from "@fortawesome/free-solid-svg-icons";
import { useEffect, useState } from "react";

const Player = ({
  currentSong,
  setCurrentSong,
  songs,
  isPlaying,
  setIsPlaying,
  audioRef,
  songInfo,
  setSongInfo,
}) => {
  const [musicBarStyle, setMusicBarStyle] = useState({});

  const playInputHandler = () => {
    setIsPlaying(!isPlaying);

    if (!isPlaying) {
      audioRef.current.play();
    } else {
      audioRef.current.pause();
    }
  };

  const randomInputHandler = () => {
    const randomSongIndex =
      Math.floor(Math.random() * songs.length) % songs.length;

    setCurrentSong(songs[randomSongIndex]);
    const playPromise = audioRef.current.play();

    if (playPromise !== undefined)
      playPromise.then((audio) => {
        audioRef.current.play();
        setIsPlaying(true);
      });
  };

  const replayInputHandler = () => {
    setSongInfo({ ...songInfo, replay: !songInfo.replay });
  };

  const previousSongInputHandler = () => {
    let previousSongIndex = songs.indexOf(currentSong) - 1;
    if (previousSongIndex < 0) previousSongIndex = songs.length - 1;
    setCurrentSong(songs[previousSongIndex]);
    const playPromise = audioRef.current.play();

    if (playPromise !== undefined)
      playPromise.then((audio) => {
        audioRef.current.play();
        setIsPlaying(true);
      });
  };

  const nextSongInputHandler = () => {
    const nextSongIndex = (songs.indexOf(currentSong) + 1) % songs.length;
    setCurrentSong(songs[nextSongIndex]);
    const playPromise = audioRef.current.play();

    if (playPromise !== undefined)
      playPromise.then((audio) => {
        audioRef.current.play();
        setIsPlaying(true);
      });
  };

  const updateRangeHandler = (e) => {
    audioRef.current.currentTime = e.target.value;
    setSongInfo({ ...songInfo, currentTime: e.target.value });
  };

  useEffect(() => {
    const barPercent = Math.floor(
      (songInfo.currentTime / songInfo.duration) * 100
    );

    if (barPercent >= 99) {
      nextSongInputHandler();
    }

    const newStyle = {
      backgroundColor: "rgb(204, 204, 204)",
      width: "100%",
      height: "100%",
      position: "absolute",
      top: "0",
      left: "0",
      transform: `translateX(${barPercent ? barPercent : 0}%`,
      pointerEvents: "none",
    };

    setMusicBarStyle(newStyle);
  }, [songInfo]);

  const getTime = (time) => {
    return (
      Math.floor(time / 60) + " : " + ("0" + Math.floor(time % 60)).slice(-2)
    );
  };

  return (
    <div className="player">
      <div className="player-container">
        <h4>{getTime(songInfo.currentTime)}</h4>
        <div className="track">
          <input
            type="range"
            min="0"
            max={songInfo.duration || 0}
            value={songInfo.currentTime}
            onChange={updateRangeHandler}
          />
          <div style={musicBarStyle}></div>
        </div>
        <h4>{songInfo.duration ? getTime(songInfo.duration) : "0:00"}</h4>
      </div>
      <div className="player-icons">
        <FontAwesomeIcon
          onClick={randomInputHandler}
          className="player-icon"
          size="2x"
          icon={faRandom}
        />
        <FontAwesomeIcon
          onClick={previousSongInputHandler}
          className="player-icon"
          size="2x"
          icon={faAngleLeft}
        />
        <FontAwesomeIcon
          onClick={playInputHandler}
          className="player-icon"
          size="2x"
          icon={isPlaying ? faPause : faPlay}
        />
        <FontAwesomeIcon
          onClick={nextSongInputHandler}
          className="player-icon"
          size="2x"
          icon={faAngleRight}
        />
        <FontAwesomeIcon
          onClick={replayInputHandler}
          className="player-icon"
          size="2x"
          icon={faSyncAlt}
        />
      </div>
    </div>
  );
};

export default Player;
