import { useEffect } from "react";

const LibrarySong = ({
  songs,
  song,
  currentSong,
  setCurrentSong,
  audioRef,
  isPlaying,
  setIsPlaying,
}) => {
  const SelectSong = (e) => {
    setCurrentSong(song);

    const playPromise = audioRef.current.play();

    if (playPromise !== undefined) {
      playPromise.then((audio) => {
        audioRef.current.play();
        setIsPlaying(true);
      });
    }

    audioRef.current.play();
  };

  useEffect(() => {
    currentSong.active = true;

    songs.forEach((s) => {
      if (s.id !== currentSong.id) s.active = false;
    });
  }, [currentSong]);

  return (
    <div
      className={`library-song ${song.active ? "selected" : ""}`}
      onClick={SelectSong}
    >
      <img src={song.cover} alt="cover" />
      <div className="library-song-info">
        <h3>{song.name}</h3>
        <h4>{song.artist}</h4>
      </div>
    </div>
  );
};

export default LibrarySong;
